import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';
import SEO from '@common/SEO';

import Layout from '@common/Layout';
import Navbar from '@common/Navbar';
import Channelling from '@common/Channelling';
import Footer from '@common/Footer';

import { Section } from '@components/global';
import ImgSingle from '@ui/img_single';
import Paragraph from '@ui/paragraph';
import ParagraphInverse from '@ui/paragraph_inverse';
import Team from '@sections/about/Team';
import SectionHeader from '@ui/sectionheader';

import about from '@static/og/about.jpg';


const paragraphTxt1 = [
  {
    content: () => ( 
      <>
      <p>This dream was to create an universal audio device that’s accessible to all hearing aid and cochlear implant users, as well as to hearing people to enjoy music with excellent sound quality.</p>
      </>
    ), 
  },
];

const paragraphTxt2 = [
  {
    content: () => ( 
      <>
      <p>Streamers are an intermediary between the hearing aid and the audio source — such as TV or phone — transmitting the audio from the source to the hearing aid.</p>

      <p>The reason we wanted to re-invent the streamer is that they're not universal, they only work for specific hearing conditions.</p> 

      <p>Most streamers don't work in combination with normal earphones, so enjoying music with a single hearing aid combined with normal earphones is impossible.</p> 

      <p>Most streamers don't work in combination with normal earphones, so enjoying music with a single hearing aid combined with normal earphones is impossible.</p>
      </>
    ), 
  },
];


const paragraphTxt3 = [
  {
    content: () => ( 
      <>

      <p>We wanted to create a modular earphone that would free people to show their personality while providing excellent sound quality — in a completely universal and accessible product. We want to free the streamer from the medicalized image, and make it like any other audio device that the users can customize and express their style.</p>

      <p>Now we've been working on this project for 3 years and have come a long way. Finally our product is ready. And we call it Hai. It is everything we dreamed of from the beginning, and we are so happy/ready to share it with you!</p>
      </>
    ), 
  },
];



const AboutUs = () => (
  <StaticQuery
    query={graphql`
      query {
        About: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "about" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        }, 
        Wip: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "wip" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        }, 
        Interview: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "interview" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        }, 
        Testing: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "testing" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },     
      }
    `}
    render={data => (
      <Layout>    
        <SEO title="About | Rearia - Hearing Technology for Everyone" desc="Our dream was to make a device that would be universal to most hearing conditions, deliver excellent sound quality and would not discriminate hearing aid users or medicalize their devices unnecessarily." img={about} url="https://rearia.io/about/"/>
        <Navbar />
        <Section id="about_us">
          <SectionHeader 
            header="We are Rearia" 
          />
        <ImgSingle
          header="Hai!" 
          text="We're Ansin and Kevin. We both share a deep love for music and tech. Our shared passions brought us together in 2017, when we finally decided to pursue our dream."
          togglecover="false"  
          toggletop="true"       
        >
          <Img fluid={data.About.childImageSharp.fluid} />
        </ImgSingle>
          <Paragraph paragraph={paragraphTxt1} caption="Ansin during an interview">
            <Img fluid={data.Interview.childImageSharp.fluid}/>
          </Paragraph>
          <ParagraphInverse paragraph={paragraphTxt2} caption="Kevin at a prototype test">
            <Img fluid={data.Testing.childImageSharp.fluid}/>
          </ParagraphInverse>
          <Paragraph paragraph={paragraphTxt3} caption="From concept to product">
            <Img fluid={data.Wip.childImageSharp.fluid}/>
          </Paragraph>
        </Section>
        <Team/>
        <Channelling/>
        <Footer />
      </Layout>
    )}
  />
);

export default AboutUs;
